@import '../../App/App.scss';

.topWrapper {
    flex: 1;
    display: flex;
    padding: 12px 20px;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
}

.bg {
    position: absolute;
    height: 100%;
    top: 0;
    left: -1000px;
    width: 3000px;

}

.title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: $BG_WHITE;
    margin-bottom: 20px;
}

.descr {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    color: $BG_WHITE;
}

@media (max-width:450px) {
    .title {

        font-size: 19px;
        line-height: 18px;
    }
}

@media (max-width:400px) {
    .title {

        font-size: 19px;
        line-height: 18px;
    }

    .TopStatusJoin_descr__9Jsr9 {
        font-weight: 500;
        font-size: 13px;
        line-height: 17px;
        color: #FFF;
    }
}